import React, { useState } from "react";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
function Map({ apiip }) {
  const [loading, setLoading] = useState(false);
  const api = (preset) => {
    setLoading(true);
    fetch(`http://${apiip}:3333/${preset}`)
      .then((data) => data.json())
      .then((data) => data.status === "success" && setLoading(false));
  };

  return (
    <div className="map">
      <h2 className="header">Map Controls</h2>
      <hr />
      {(loading && (
        <div className="center">
          <Loader type="TailSpin" color="#253031" height={100} width={100} />
        </div>
      )) || (
        <>
          <button onClick={() => api("america")}> America</button>
          <button onClick={() => api("metars")}> Metars</button>
          <button onClick={() => api("red")}> Red</button>
          <button onClick={() => api("blue")}> Blue</button>
          <button onClick={() => api("green")}> Green</button>
          <button onClick={() => api("off")}> Off</button>
        </>
      )}
    </div>
  );
}

export default Map;
