import React, { useState } from "react";

function Ip({ setApiip, apiip }) {
  const [ip, setIp] = useState("");
  const save = () => {
    setApiip(ip);
    setIp("");
    localStorage.setItem("ip_address_for_metar_map", apiip);
  };
  return (
    <div className="ip">
      <h2 className="info">
        This is the "KEY_IP" printed out when running the boot file. It is also
        the IP address of this site.
      </h2>
      <br />
      <h2 className="info">Example: "192.168.0.12"</h2>
      <div className="info">
        <input type="text" value={ip} onChange={(x) => setIp(x.target.value)} />
        <button onClick={() => save()}>Assign IP</button>
        <h2 className="info special">
          This will be saved in cookies for the next time you access this site
        </h2>
      </div>
    </div>
  );
}

export default Ip;
