import React, { useEffect, useState } from "react";
import Map from "./content/Map";
import Ip from "./content/Ip";
import Attrib from "./content/Attrib";

const Content = () => {
  const [apiip, setApiip] = useState(null);
  useEffect(() => {
    if (localStorage.getItem("ip_address_for_metar_map") !== null)
      setApiip(localStorage.getItem("ip_address_for_metar_map"));
  }, []);
  return (
    <div className="content">
      <hr />
      {(apiip && <Map apiip={apiip} setApiip={setApiip} />) || (
        <Ip apiip={apiip} setApiip={setApiip} />
      )}
      <button onClick={() => setApiip(null)}>Reset IP</button>
      <Attrib />
    </div>
  );
};

export default Content;
